.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.main-bg{height: 500px;background-image: url(../img/main_nk.jpg);background-size: cover;background-position: center;}
.nk01{background-image: url(../img/nk1.jpg);width: 60%;height: 200px;background-size: cover;background-position: center;}
.nk02{background-image: url(../img/nk2.jpg);width: 60%;height: 200px;background-size: cover;background-position: center;}
.nk03{background-image: url(../img/nk3.jpg);width: 60%;height: 200px;background-size: cover;background-position: center;}

a{text-decoration: auto;}

.start {
  opacity: 0;
}
.end {
  opacity: 1;
  transition: opacity 0.5s;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
